body {
  margin: 0;
}

code {
  font-family: var(--lc-font-family-code);
}

@keyframes lc-spin{
  to{
    transform: rotate(360deg);
  }
}

@keyframes lc-ping{
  75%, 100%{
    transform: scale(2);
    opacity: 0;
  }
}

@keyframes lc-pulse{
  50%{
    opacity: .5;
  }
}

@keyframes lc-bounce{
  0%, 100%{
    transform: translateY(-25%);
    animation-timing-function: cubic-bezier(0.8,0,1,1);
  }

  50%{
    transform: none;
    animation-timing-function: cubic-bezier(0,0,0.2,1);
  }
}

*, ::before, ::after{
  --tw-shadow: 0 0 #0000;
}

*, ::before, ::after{
  --tw-ring-inset: var(--tw-empty,/*!*/ /*!*/);
  --tw-ring-offset-width: 0px;
  --tw-ring-offset-color: #fff;
  --tw-ring-color: rgba(59, 130, 246, 0.5);
  --tw-ring-offset-shadow: 0 0 #0000;
  --tw-ring-shadow: 0 0 #0000;
}

.lc-button-group .lc-button {
    margin: 0;
    border-radius: 0;
  }

.lc-button-group .lc-button:first-child {
      border-top-left-radius: 6px;
      border-bottom-left-radius: 6px;
    }

.lc-button-group .lc-button:last-child {
      border-top-right-radius: 6px;
      border-bottom-right-radius: 6px;
    }

.lc-button-group .lc-button:not(:first-child):not(:last-child) {
      border-left-color: rgba(0, 0, 0, 0.25);
      border-right-color: rgba(0, 0, 0, 0.25);
    }

.lc-button-group .lc-button:focus {
      z-index: 1;
    }

.lc-chart-label {
    display: flex;
    align-items: center;
    white-space: nowrap;
    font-size: 14px;
    line-height: 1.5;
    --tw-text-opacity: 1;
    color: rgba(var(--lc-color-black), var(--tw-text-opacity));
  }

.lc-chart-label-name {
      font-weight: 700;
    }

.lc-chart-label-color {
      display: inline-block;
      width: 11px;
      height: 11px;
      margin-right: 12px;
      border-radius: 100%;
    }

.lc-chart-label-value::before {
        content: ': ';
      }

.lc-chart-legend {
    display: flex;
    flex-flow: row wrap;
    list-style: none;
    padding: 16px 0;
    margin: -theme('spacing.4') -theme('spacing.16');
  }

.lc-chart-legend-item {
      padding: 4px 16px;
    }

.lc-chart-legend-item-label {
        font-weight: 400;
        --tw-text-opacity: 1;
        color: rgba(var(--lc-color-gray-dark), var(--tw-text-opacity));
      }

.lc-chart-legend-item-value {
        margin-left: 4px;
      }

.lc-chart-legend-item-button.lc-button {
          padding: 8px 16px;
          border-radius: 100px;
          border-width: 1px;
          border-style: solid;
          --tw-text-opacity: 1;
          color: rgba(var(--lc-color-gray-light), var(--tw-text-opacity));
          --tw-bg-opacity: 1;
          background-color: rgba(var(--lc-color-gray-lightest), var(--tw-bg-opacity));
          --tw-border-opacity: 1;
          border-color: rgba(var(--lc-color-gray-lightest), var(--tw-border-opacity));
        }

.lc-chart-legend-item-button.lc-button .lc-chart-label {
            color: inherit;
          }

.lc-chart-legend-item-button.lc-button .lc-button-content {
            display: flex;
            align-items: center;
          }

.lc-chart-legend-item-button.lc-button:hover {
            --tw-text-opacity: 1;
            color: rgba(var(--lc-color-gray), var(--tw-text-opacity));
            --tw-border-opacity: 1;
            border-color: rgba(var(--lc-color-gray-lighter), var(--tw-border-opacity));
          }

.lc-chart-legend-item-active .lc-chart-legend-item-button.lc-button {
        --tw-text-opacity: 1;
        color: rgba(var(--lc-color-gray-dark), var(--tw-text-opacity));
        background-color: transparent;
        --tw-border-opacity: 1;
        border-color: rgba(var(--lc-color-gray-lighter), var(--tw-border-opacity));
        box-shadow: 0px 8px 20px -12px rgba(0, 0, 0, 0.15);
      }

.lc-chart-legend-interactive {
      padding: 8px 0;
      margin: -theme('spacing.8') -theme('spacing.8');
    }

.lc-chart-legend-interactive .lc-chart-legend-item {
      padding: 8px 8px;
    }

.lc-chart-tooltip {
    position: absolute;
    pointer-events: none;
    transform: translate(-50%, -100%);
    transition: all 0.3s ease-in-out;
    opacity: 0;
  }

.lc-chart-tooltip-content {
      --tw-bg-opacity: 1;
      background-color: rgba(var(--lc-color-white), var(--tw-bg-opacity));
      font-size: 14px;
      line-height: 1.5;
      padding-top: 16px;
      padding-bottom: 16px;
      padding-left: 20px;
      padding-right: 20px;
      border-radius: 8px;
      border-width: 1px;
      --tw-border-opacity: 1;
      border-color: rgba(var(--lc-color-gray-lighter), var(--tw-border-opacity));
      text-align: center;
      box-shadow: 0px 12px 20px rgba(0, 0, 0, 0.1);
    }

.lc-chart-tooltip-loaded {
      opacity: 1;
    }

.lc-drawer-content {
    padding: 24px;
    max-width: 100%;
  }

.lc-file-uploader {
  --tw-text-opacity: 1;
  color: rgba(var(--lc-color-gray-dark), var(--tw-text-opacity));
  margin-bottom: 24px;
}

.lc-file-uploader-disabled {
    transition-property: all;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
    transition-duration: 150ms;
    pointer-events: none;
    opacity: 0.6;
  }

.lc-file-uploader-dropzone {
    display: block;
    padding: 32px;
    text-align: center;
    border-style: solid;
    border-width: 1px;
    transition-property: all;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
    transition-duration: 150ms;
    border-radius: 4px;
    border-color: rgb(var(--lc-color-gray-lighter));
    background-color: transparent;
  }

.lc-file-uploader-dropzone:hover {
      cursor: pointer;
      --tw-text-opacity: 1;
      color: rgba(var(--lc-color-primary), var(--tw-text-opacity));
      --tw-border-opacity: 1;
      border-color: rgba(var(--lc-color-primary), var(--tw-border-opacity));
      --tw-bg-opacity: 1;
      background-color: rgba(var(--lc-color-primary), var(--tw-bg-opacity));
      --tw-bg-opacity: 0.05;
    }

.lc-file-uploader-dropzone-active {
      border-style: dashed;
    }

.lc-file-uploader-dropzone-accept {
      color: rgb(var(--lc-color-primary)) !important;
      border-color: rgb(var(--lc-color-primary)) !important;
      background-color: rgb(var(--lc-color-primary), 0.05) !important;
    }

.lc-file-uploader-dropzone-reject {
      color: rgb(var(--lc-color-danger)) !important;
      border-color: rgb(var(--lc-color-danger)) !important;
      background-color: rgba(var(--lc-color-danger), 0.05) !important;
    }

.lc-file-uploader-dropzone-placeholder {
      display: inline-block;
      width: 64px;
    }

.lc-file-uploader-dropzone-text {
      font-size: 14px;
      line-height: 1.5;
      margin-top: 24px;
    }

.lc-file-uploader-dropzone-files {
      display: inline-flex;
      flex-wrap: wrap;
      list-style-type: none;
      margin: -12px;
      padding: 0;
      align-items: flex-start;
      justify-content: center;
    }

.lc-file-uploader-dropzone-files li {
        margin: 0;
        padding: 12px;
        flex: 1 1 16.666667%;
        min-width: 16.666667%;
        max-width: 240px;
      }

.lc-file-uploader-dropzone-file {
      transition-property: all;
      transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
      transition-duration: 150ms;
      --tw-text-opacity: 1;
      color: rgba(var(--lc-color-primary), var(--tw-text-opacity));
      --tw-bg-opacity: 1;
      background-color: rgba(var(--lc-color-white), var(--tw-bg-opacity));
      padding: 4px;
      width: 100%;
      max-width: 100%;
    }

.lc-file-uploader-dropzone-file:hover {
        --tw-text-opacity: 1;
        color: rgba(var(--lc-color-primary-dark), var(--tw-text-opacity));
        --tw-bg-opacity: 1;
        background-color: rgba(var(--lc-color-white), var(--tw-bg-opacity));
      }

.lc-file-uploader-dropzone-file-thumb {
        position: relative;
        display: flex;
        align-items: center;
        justify-content: center;
        --tw-bg-opacity: 1;
        background-color: rgba(var(--lc-color-gray-lightest), var(--tw-bg-opacity));
        width: 100%;
        border-radius: 4px;
      }

.lc-file-uploader-dropzone-file-thumb-image {
          display: block;
          max-width: 100%;
          height: 160px;
          object-fit: contain;
        }

.lc-file-uploader-dropzone-file-thumb-overlay {
          position: absolute;
          display: flex;
          align-items: center;
          justify-content: center;
          top: 0;
          right: 0;
          bottom: 0;
          left: 0;
          opacity: 0;
          transition-property: all;
          transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
          transition-duration: 150ms;
          border-radius: 4px;
          --tw-bg-opacity: 1;
          background-color: rgba(var(--lc-color-black), var(--tw-bg-opacity));
          --tw-bg-opacity: 0.4;
        }

.lc-file-uploader-dropzone-file:hover .lc-file-uploader-dropzone-file-thumb-overlay {
            opacity: 1;
          }

.lc-file-uploader-dropzone-file-remove-icon {
        --tw-text-opacity: 1;
        color: rgba(var(--lc-color-white), var(--tw-text-opacity));
      }

.lc-file-uploader-dropzone-file-name {
        font-size: 12px;
        line-height: 1.5;
        margin-top: 8px;
        word-break: break-all;
      }

.lc-file-uploader-errors {
    --tw-text-opacity: 1;
    color: rgba(var(--lc-color-danger), var(--tw-text-opacity));
    margin: 0;
    margin-top: 16px;
    padding: 0;
    padding-left: 16px;
  }

.lc-file-uploader-errors li + li {
      margin-top: 8px;
    }

.lc-file-uploader-footer {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    padding-top: 16px;
    margin: -8px;
  }

.lc-file-uploader-actions {
    display: flex;
    align-items: center;
    padding: 8px;
  }

.lc-file-uploader-restrictions {
    padding: 8px;
  }

#lc-unsaved-changes-modal .flex-spacer {
    flex: 1;
  }

.lc-icon {
  width: 24px;
  height: 24px;
}

.lc-icon,
  .lc-icon-decorative {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    box-sizing: border-box;
    vertical-align: middle;
  }

.lc-icon svg {
    width: 100%;
    height: 100%;
    color: inherit;
  }

.lc-icon:not(.lc-icon-color) svg,
  .lc-icon:not(.lc-icon-color) path {
    fill: currentColor;
  }

.lc-icon-decorative {
    width: 32px;
    height: 32px;
    border-radius: 4px;
  }

.lc-icon-decorative .lc-icon {
      width: 20px;
      height: 20px;
    }

.lc-icon-decorative:not(.lc-icon-color) svg,
    .lc-icon-decorative:not(.lc-icon-color) path {
      fill: white;
    }

.lc-button.lc-icon-button {
  display: inline-flex;
  min-height: auto;
  padding: 0;
  border: none;
  height: 36px;
  width: 36px;
  min-width: 36px;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
}

.lc-icon-button {
  --lc-icon-button-color: var(--lc-color-gray-dark);
  --lc-icon-button-hover-bg-color: var(--lc-color-gray-lighter);
  --lc-icon-button-hover-bg-transparency: 1;
  background-color: transparent;
  color: rgba(var(--lc-icon-button-color), 1);
}

.lc-icon-button:hover {
    background-color: rgba(var(--lc-icon-button-hover-bg-color), var(--lc-icon-button-hover-bg-transparency));
  }

.lc-icon-button.match-text-color {
    --lc-icon-button-color: inherit;
  }

.lc-icon-button.match-text-color:hover {
      background-color: transparent;
    }

.lc-icon-button.match-text-color:hover::after {
        content: '';
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        background-color: currentColor;
        opacity: 0.2;
        z-index: 0;
        border-radius: 50%;
      }

.lc-image-cropper-modal {
    position: relative;
  }

.lc-image-cropper-modal .MuiCard-root {
      overflow-y: scroll;
      overflow: auto;
      max-height: calc(100vh - 40px);
      max-width: calc(100vw - 40px);
    }

.lc-image-cropper-modal .MuiCardContent-root {
      padding-top: 0 !important;
      padding-bottom: 0 !important;
    }

.lc-image-cropper-header {
    padding-left: 16px;
    padding-right: 16px;
    padding-top: 20px;
    padding-bottom: 20px;
    margin-left: -16px;
    margin-right: -16px;
    margin-bottom: 0;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    --tw-bg-opacity: 1;
    background-color: rgba(var(--lc-color-white), var(--tw-bg-opacity));
    width: 100%;
    box-sizing: content-box;
    z-index: 10;
  }

.lc-image-cropper-cropper {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 24px;
    --tw-bg-opacity: 1;
    background-color: rgba(var(--lc-color-gray-lightest), var(--tw-bg-opacity));
    border-radius: 4px;
    background-size: 20px 20px;
    background-position: 0 0, 0 10px, 10px -10px, -10px 0px;
  }

.lc-image-cropper-cropper .ReactCrop {
      margin: 0 !important;
    }

.lc-image-cropper-preview {
    display: flex;
    align-items: center;
  }

.lc-image-cropper-preview-image {
      position: relative;
      display: flex;
      align-items: center;
      justify-content: center;
      width: 48px;
      height: 48px;
      --tw-bg-opacity: 1;
      background-color: rgba(var(--lc-color-gray-lighter), var(--tw-bg-opacity));
      border-radius: 4px;
      margin-right: 8px;
    }

.lc-image-cropper-preview-image img {
        width: 40px;
        height: 40px;
        object-fit: contain;
      }

.lc-image-cropper-footer {
    display: flex;
    align-items: center;
    padding: 24px;
    position: -webkit-sticky;
    position: sticky;
    bottom: 0;
    --tw-bg-opacity: 1;
    background-color: rgba(var(--lc-color-white), var(--tw-bg-opacity));
    width: 100%;
    margin-left: -24px;
    margin-right: -24px;
    box-sizing: content-box;
  }

.lc-image-cropper-actions {
    margin: -4px;
  }

.lc-image-cropper-actions .lc-button {
      margin: 4px;
    }

.MuiFormControl-root {
  width: 100%;
}

.MuiFormControl-root.lc-input {
    display: flex;
    margin-bottom: 24px;
  }

.MuiFormControl-root.lc-input .MuiOutlinedInput-root {
      border-radius: 6px;
    }

.MuiFormControl-root.lc-input input:not([type='color']) {
      border-radius: 6px;
      box-sizing: content-box;
      height: 28px;
      padding-top: 10px;
      padding-bottom: 10px;
      zoom: 1;
    }

.MuiFormControl-root.lc-input-border-white .MuiOutlinedInput-notchedOutline {
        border-color: white;
      }

.MuiFormControl-root.lc-input.dark-mode .MuiFormLabel-root {
        color: white;
      }

.MuiFormControl-root.lc-input.dark-mode .MuiInputBase-input {
        color: white;
      }

.MuiFormControl-root.lc-input.dark-mode .MuiInput-underline:before {
        border-bottom: 1px solid rgba(255, 255, 255, 0.42);
      }

.MuiFormControl-root.lc-input.dark-mode .MuiInput-underline:after {
        border-bottom: 2px solid white;
      }

.MuiFormControl-root.lc-input.dark-mode input:-internal-autofill-selected {
        background-color: rbga(255, 255, 255, 0.42) !important;
      }

.MuiFormControl-root.lc-input.dark-mode .MuiInputAdornment-root *,
      .MuiFormControl-root.lc-input.dark-mode .MuiFormHelperText-root,
      .MuiFormControl-root.lc-input.dark-mode .MuiFormHelperText-root.Mui-error {
        color: white;
      }

.MuiFormControl-root .MuiTypography-body1,
  .MuiFormControl-root .MuiInputBase-root,
  .MuiFormControl-root .MuiInputLabel-root,
  .MuiFormControl-root .MuiFormHelperText-root,
  .MuiFormControl-root .MuiInputAdornment-root {
    font-size: inherit;
  }

.MuiFormControl-root .MuiTypography-body1, .MuiFormControl-root .MuiInputBase-root, .MuiFormControl-root .MuiInputLabel-root, .MuiFormControl-root .MuiFormHelperText-root, .MuiFormControl-root .MuiInputAdornment-root{
